import React, { useEffect, useState } from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { useForm } from "react-hook-form";
import { useAlert } from "react-alert";
import { showAlert } from "./commonComponent/alertManager";
import { editProfile, viewProfile } from "../apiServices/home/homeHttpService";
import { useQuery } from "@tanstack/react-query";

function Profile() {
  const [file, setFile] = useState(null);
  const [profileUpdated, setProfileUpdated] = useState(Date.now());
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });

  const alert = useAlert();

  const { data: details, isSuccess } = useQuery({
    queryKey: ["admin"],
    queryFn: viewProfile,
    select: (data) => data.results,

    onError: (error) => {
      alert.error(error.message);
    },
  });

  useEffect(() => {
    if (isSuccess && details) {
      document
        .getElementById("admin-img")
        .setAttribute("src", details?.image ? details?.image : "");

      const defaultValue = {
        fullName: details?.fullName || "",
        email: details?.email || "",
        mobileNumber: details?.mobileNumber || "",
      };

      reset(defaultValue);
    }
  }, [isSuccess, details, reset]);

  const handleImageUpload = (event) => {
    const selectedFile = event.target.files[0];
    setFile(selectedFile);
  };

  const onSubmit = async (data) => {
    const formData = new FormData();
    if (file?.name) {
      formData.append("image", file);
    }

    Object.keys(data).forEach((key) => {
      formData.append(key, data[key]);
    });
    const response = await editProfile(formData);
    if (!response.error) {
      setProfileUpdated(Date.now());
      showAlert(alert, response.message, { timeout: 3000 });
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };
  return (
    <>
      <Header update={profileUpdated} />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <div className="row mx-0">
            <div className="col-12">
              <form
                className="userdetails row align-items-center"
                onSubmit={handleSubmit(onSubmit)}
              >
                <div className="col-md-auto">
                  <div className="userdetails_img position-relative">
                    <img
                      src={
                        file
                          ? URL.createObjectURL(file)
                          : "../../assets/img/user1.png"
                      }
                      alt=""
                      id="admin-img"
                    />

                    <label htmlFor="photo-upload" className="editicon">
                      <i className="fas fa-edit" />
                    </label>

                    <input
                      id="photo-upload"
                      type="file"
                      onChange={(e) => handleImageUpload(e)}
                    />
                  </div>
                </div>
                <div className="col">
                  <div className="userdetails_main ps-md-2 pe-md-5">
                    <h2>{details?.fullName} </h2>
                    <button type="submit" className="edit_btn">
                      Edit Profile
                    </button>
                  </div>
                </div>
                <div className="col-12 mt-4">
                  <div className="row form-design">
                    <div className="form-group col-md-4">
                      <label htmlFor>Full Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="fullName"
                        id="fullName"
                        {...register("fullName", {
                          required: true,
                        })}
                      />
                      {errors.fullName &&
                        errors.fullName.type === "required" && (
                          <p className="form-error">This field is required</p>
                        )}
                    </div>
                    <div className="form-group col-md-4">
                      <label htmlFor>Email Address</label>
                      <input
                        type="email"
                        className="form-control"
                        name="email"
                        id="email"
                        {...register("email", {
                          required: "This field is required",
                          pattern: {
                            value:
                              /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                            message: "Invalid email address",
                          },
                        })}
                      />

                      {errors?.email && (
                        <p className="form-error mt-1">
                          {errors.email.message}
                        </p>
                      )}
                    </div>
                    <div className="form-group col-md-4">
                      <label htmlFor>Mobile Number</label>
                      <input
                        className="form-control"
                        type="number"
                        name="mobileNumber"
                        {...register("mobileNumber", {
                          required: true,
                          maxLength: 9,
                          minLength: 9,
                        })}
                      />

                      {errors.mobileNumber &&
                        errors.mobileNumber.type === "required" && (
                          <p className="form-error">This field is required</p>
                        )}

                      {errors.mobileNumber &&
                        errors.mobileNumber.type === "maxLength" && (
                          <p className="form-error">
                            Please enter 9 digit number
                          </p>
                        )}
                      {errors.mobileNumber &&
                        errors.mobileNumber.type === "minLength" && (
                          <p className="form-error">
                            Please enter 9 digit number
                          </p>
                        )}
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Profile;
